.container
  display: flex
  height: 88.8vh
  *
    font-family: Gilroy
  
.left 
  overflow: hidden
  display: flex
  flex-wrap: wrap
  flex-direction: column
  justify-content: center
  animation-name: left
  animation-duration: 1s
  animation-fill-mode: both
  animation-delay: 1s


.right 
  flex: 1
  background-color: black
  transition: 1s
  background-image: url("../../assets/prog1.jpg")
  background-size: cover
  background-repeat: no-repeat
  background-position: center

.right2
  flex: 1
  background-color: black
  transition: 1s
  background-image: url("../../assets/prog2.jpg")
  background-size: cover
  background-repeat: no-repeat
  background-position: center


.header > h2 
  margin: 0
  color: #4f46a5


.header > h4 
  margin-top: 10px
  font-weight: normal
  font-size: 15px
  color: rgba(0,0,0,.4)

.form 
  max-width: 80%
  display: flex
  flex-direction: column


.form > p 
  text-align: right


.form > p > a 
  color: #000
  font-size: 14px


.form-field 
  height: 46px
  padding: 0 16px
  border: 2px solid #ddd
  border-radius: 4px
  font-family: 'Rubik', sans-serif
  outline: 0
  transition: .2s
  margin-top: 20px


.form-field:focus 
  border-color: #0f7ef1


.form > button 
  padding: 12px 10px
  border: 0
  background: blue
  border-radius: 3px
  margin-top: 30px
  color: #fff
  letter-spacing: 1px
  font-family: 'Rubik', sans-serif


.animation 
  animation-name: move
  animation-duration: .4s
  animation-fill-mode: both
  animation-delay: 2s


.a1 
  animation-delay: 2s
  font-size: 25px

.a2 
  animation-delay: 2.1s

.a3 
  animation-delay: 2.2s

.a4 
  animation-delay: 2.3s

.a5 
  animation-delay: 2.4s
  display: none

.a6 
  animation-delay: 2.5s
  cursor: pointer


@keyframes move 
  0% 
    opacity: 0
    visibility: hidden
    transform: translateY(-40px)
  

  100% 
    opacity: 1
    visibility: visible
    transform: translateY(0)
  


@keyframes left 
  0% 
    opacity: 0
    width: 0
  

  100% 
    opacity: 1
    padding: 20px 40px
    width: 440px
  
  
@media (max-width: 768px)
  .container
    background-color: #e9e9e9
    .right
      display: none
    .right2
      display: none  
    .left
      margin: 0 auto
      border: 1px solid #d5d5d5
      margin-top: 75px
      max-width: 70%
      border-radius: 15px
      background-color: #fff
      height: 70%
      input
        width: 125%
      button
        width: 125%
      .a1
        font-size: 30px